@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: "Poppins" !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  background-image: url(/static/media/background.9c31d996.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  color: #631f48 !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.menu-bar-wrapper .nav-link{color:#631f48 !important}

.wrapper{margin-top:100px;margin-bottom:50px}.wrapper hr{max-width:352px;border:1px solid #631f48;border-radius:2px}.wrapper h3{font-weight:700;font-size:25px}

.label{font-weight:700;font-size:16px}.label p{margin:auto auto auto 0}.value{font-weight:400;font-size:16px}

.timeline{padding:46px 0;position:relative}.timeline:before{content:"";position:absolute;top:0px;left:calc(31% + 15px);bottom:85px;width:4px;background:#631f48}.timeline:after{content:"";display:table;clear:both}.entry{clear:both;text-align:left;position:relative}.entry .title{margin-bottom:0.5em;width:33%;padding-right:30px;text-align:right;position:relative}.entry .title:before{content:"";position:absolute;width:15px;height:15px;border:4px solid salmon;background-color:#fff;border-radius:100%;top:15%;right:-8px;z-index:99}.entry .title h3{margin:0;font-size:16px;color:#631f48}.entry .title p{margin:0;font-size:100%}.entry .body{margin:0 0 3em;float:right;width:66%;padding-left:30px}.entry .body p{line-height:1.4em}.entry .body p:first-child{margin-top:-40px;font-weight:400}.entry .body ul{color:#aaa;padding-left:0;list-style-type:none}.entry .body ul li:before{content:"–";margin-right:0.5em}.t-title{font-weight:700 !important;font-size:16px !important;color:#631f48;margin-bottom:5px}.t-body{font-weight:400 !important;font-size:14px !important;line-height:25px !important;color:#631f48}

.progress-wrapper{margin-bottom:13px}.progress-wrapper .progressbar{height:10px}.progress-wrapper p{font-weight:500}


.contact-wrapper p{font-weight:500;font-size:18px}


.portfolio-wrapper .tab-text{color:#631f48}.portfolio-wrapper .content .thumb{position:relative}.portfolio-wrapper .content a{position:absolute;top:0;left:15px;right:15px;border-radius:18px;bottom:0;background-color:rgba(0,0,0,0.6);opacity:0;transition:opacity 0.3s;box-shadow:0 4px 15px rgba(0,0,0,0.5)}.portfolio-wrapper .content a:hover{opacity:1}


.welcome-page img{width:250px;height:250px}.welcome-page h4{font-weight:500;font-size:20px;margin:0;padding-left:2px}.welcome-page h1{font-weight:700;font-size:48px;margin:0}.welcome-page h3{font-weight:700;font-size:25px}.welcome-page p{font-weight:400;font-size:18px}

.page-wrapper{margin-top:50px;background:rgba(255,255,255,0.35);border:1px solid #c2c2c2;-webkit-backdrop-filter:blur(200px);backdrop-filter:blur(200px);border-radius:45px;margin-bottom:50px;padding-bottom:50px;padding-top:40px;min-height:500px}

