.portfolio-wrapper {
    .tab-text {
        color: #631f48;
    }
    .content {
        .thumb {
            position: relative;
        }
        a {
            position: absolute;
            top: 0;
            left: 15px;
            right: 15px;
            border-radius: 18px;
            bottom: 0;
            background-color: rgba(0, 0, 0, 0.6);
            opacity: 0;
            transition: opacity 0.3s;
            box-shadow: 0 4px 15px rgba(0, 0, 0, 0.5);
        }
    
        a:hover {
            opacity: 1;
        }
    }
}