.timeline {
    padding: 46px 0;
    position: relative;
  }
  .timeline:before {
    content: "";
    position: absolute;
    top: 0px;
    left: calc(31% + 15px);
    bottom: 85px;
    width: 4px;
    background: #631f48;
  }
  .timeline:after {
    content: "";
    display: table;
    clear: both;
  }
  
  .entry {
    clear: both;
    text-align: left;
    position: relative;
  }
  .entry .title {
    margin-bottom: 0.5em;
    /* float: left; */
    width: 33%;
    padding-right: 30px;
    text-align: right;
    position: relative;
  }
  .entry .title:before {
    content: "";
    position: absolute;
    width: 15px;
    height: 15px;
    border: 4px solid salmon;
    background-color: #fff;
    border-radius: 100%;
    top: 15%;
    right: -8px;
    z-index: 99;
  }
  .entry .title h3 {
    margin: 0;
    font-size: 16px;
    color: #631f48;
  }
  .entry .title p {
    margin: 0;
    font-size: 100%;
  }
  .entry .body {
    margin: 0 0 3em;
    float: right;
    width: 66%;
    padding-left: 30px;
  }
  .entry .body p {
    line-height: 1.4em;
  }
  .entry .body p:first-child {
    margin-top: -40px;
    font-weight: 400;
  }
  .entry .body ul {
    color: #aaa;
    padding-left: 0;
    list-style-type: none;
  }
  .entry .body ul li:before {
    content: "–";
    margin-right: 0.5em;
  }
  
  .t-title {
    font-weight: 700 !important;
    font-size: 16px !important;
    color: #631f48;
    margin-bottom: 5px;
  }
  
  .t-body {
    font-weight: 400 !important;
    font-size: 14px !important;
    line-height: 25px !important;
    color: #631f48;
  }